// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}
.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}
.outfit-regular {
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.outfit-semibold {
  font-family: "Outfit", sans-serif;
  font-weight: 600;
}
// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}
.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}
.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$pointColor: #41A8BF;
$bgColor: #FAF3EB;

.About {
  height: calc(100vh - 196px);
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

.intro {
  padding: 5% 6%;
  border-bottom: 2px solid var(--pointColor);

  > div {
    > p {
      line-height: 100%;
      font-family: "Akaya Kanadaka", "system-ui";
      font-size: 15em;
      color: $bgColor;
      text-shadow:
        -1px -1px 0 var(--pointColor),
        1px -1px 0 var(--pointColor),
        -1px 1px 0 var(--pointColor),
        1px 1px 0 var(--pointColor);

      &:nth-of-type(2) {
        width: 100%;
        text-align: right;
        color: var(--pointColor);
      }
      
    }

    .intro_text {
      padding-top: 5%;
      // padding-bottom: 3%;
      p {
        text-align: center;
        color: var(--pointColor);
        font-size: 0.9em;
        opacity: 0.7;
        font-style: italic;
      }
    }
        
  }
}

.main {
  display: flex;
  // width: 100%;
  min-height: 0;

  .side {
    padding: 20px;
    min-width: 278px;
    // height: calc(100vh - 196px);
    border-right: 2px solid var(--pointColor);

    ul {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      line-height: 25px;
      height: 100%;

      li {        
        color: var(--pointColor);

        &:first-child {
          font-family: "Akaya Kanadaka", "Salsa";
          font-size: 1.8em;
          padding-bottom: 10%;
        }

        &:not(:first-child) {
          font-size: 0.9em;

          span {
            font-weight: 600;
          }
        }
      }
    }
  }

  .content {
    flex: 1;
    // width: 100%;
    height: calc(100vh - 196px);
    min-height: 0;
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    .about_tit {
      text-align: center;
      width: 100%;
      line-height: 78px;
      font-family: "Akaya Kanadaka", "Salsa";
      font-size: 1.8em;
      color: var(--pointColor);
      border-bottom: 2px solid var(--pointColor);

      &:nth-of-type(3) {border-top: 2px solid var(--pointColor);}
    }

    .about_me {
      padding: 3%;
      display: flex;
      justify-content: center;
      >.img {
        width: 200px;
        height: 200px;
        border: 2px solid var(--pointColor);
      }
      >.text {
        width: 800px;
        height: 200px;
        margin-left: 3%;
        background-color: #eee;
      }
    }

    .skills {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      // height: 400px;

      > li {
        width: 16.666666666666666666666666666667%;
        padding: 20px;
        color: var(--pointColor);
        text-align: center;
        border-bottom: 2px solid var(--pointColor);

        // @for $i from 1 through 6 {
        //   &:nth-of-type(#{$i}) {
        //     border-bottom: 2px solid var(--pointColor);
        //   }
        // }

        &:not(:nth-of-type(6), :nth-of-type(12)) {
          border-right: 2px solid var(--pointColor);
        }

        &:last-child {
          background-color: var(--pointColor);
        }

        .my_skill {
          fill: var(--pointColor);
        }
      }
    }

    .About_bottom {
      display: flex;

      > div {
        color: var(--pointColor);

        &.copyright {
          padding: 2%;
          width: 55%;
          border-right: 2px solid var(--pointColor);

          p {
            font-size: 0.9em;
          }
        }

        &.project_go {
          width: 45%;
          h4 {
            cursor: pointer;
            width: 100%;
            height: 100%;
            padding-top: 2%;
            padding-left: 2%;
            font-family: "Akaya Kanadaka", "Salsa";
            font-size: 1.5em;
          }
        }
      }
    }
  }
}